@font-face {
    font-family: 'Museo';
    src: url('./font/font.museo.500.eot');
    src: url('./font/font.museo.500.eot?#iefix') format('embedded-opentype'),
        url('./font/font.museo.500.woff2') format('woff2'),
        url('./font/font.museo.500.woff') format('woff'),
        url('./font/font.museo.500.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
}
